 }  // -webkit-tap-highlight-color: $selection-color

// Basic Styles
html {
  font-size: inherit;
  line-height: 1.3;
  font-size: 16px; }

html,
body {
  height: 100%;
  padding: 0;
  margin: 0;
  min-width: 320px; }

body {
  background: $color-white;
  font-size: rem(14);
  font-family: $primary-font;
  color: $primary-color;
  -webkit-font-smoothing: antialiased;
  -webkit-text-size-adjust: 100%; }

//  Typography
.h1, .h2, .h3, .h4, .h5, .h6, h1, h2, h3, h4, h5, h6 {
  color: $primary-color;
  font-weight: 400;
  text-decoration: none;
  margin: 0 0 15px 0;
  font-family: $font-roboto-bold; }

.h1, h1 {
  font-size: rem(35);
  line-height: 1.05;
  letter-spacing: -1px;
  margin-bottom: 25px;
  text-transform: uppercase;
  font-family: $font-roboto; }

.h2, h2 {
  font-size: rem(30);
  line-height: 1.14;
  margin-bottom: 20px;
  text-transform: uppercase;
  font-family: $font-roboto; }

.h3, h3 {
  font-size: rem(22);
  line-height: 1.38; }


.h4, h4 {
  font-size: rem(19);
  line-height: 1.38; }

.h5, h5 {
  font-size: rem(17); }

.h6, h6 {
  font-size: rem(15); }

.small, small {
  font-size: rem(12);
  line-height: 1.2; }

p {
  line-height: 22px;
  margin: 0 0 20px 0; }

em, i, .em, .i {
  font-style: italic; }

strong, b, .strong, .b {
  font-weight: 400;
  font-family: $font-roboto-bold; }

hr {
  border: solid #ddd;
  border-width: 1px 0 0;
  clear: both;
  margin: 10px 0 30px;
  height: 0; }

// Links
a, .link {
  color: $link-color;
  text-decoration: underline;
  outline: none;
  transition: color 0.35s, background 0.35s, border 0.35s;

  &:hover, &:focus, &:active {
    color: $link-color;
    text-decoration: none;
    outline: none; } }

a.link {
  &:hover, &:focus, &:active {
    text-decoration: none; } }

.container {
  margin: 0 auto;
  // width: 1280px
  width: 100%;
  min-width: 320px;
  padding: 0 15px;
  .section {
    min-width: auto; } }

/* text align */
.text {
  &-center, &--center {
    text-align: center; }
  &-left, &--left {
    text-align: left; }
  &-right, &--right {
    text-align: right; }
  &-nowrap, &--nowrap {
    white-space: nowrap !important; } }

/* loader */
.loader {
  width: 100%;
  height: 100vh;
  left: 0;
  top: 0;
  position: fixed;
  z-index: 10000;
  background-color: $color-white;
  &__block {
    background: $color-1;
    animation: load1 1s infinite ease-in-out;
    width: 1em;
    height: 4em;
    position: absolute;
    top: 50%;
    left: 50%;
    color: $color-1;
    text-indent: -9999em;
    font-size: 11px;
    animation-delay: -0.16s;
    transform: translate(-50%,-50%);
    &:before, &:after {
      background: $color-1;
      animation: load1 1s infinite ease-in-out;
      width: 1em;
      height: 4em; }
    &:before, &:after {
      position: absolute;
      top: 0;
      content: ''; }
    &:before {
      left: -1.5em;
      animation-delay: -0.32s; }
    &:after {
      left: 1.5em; } }
  @-webkit-keyframes load1 {
    0%,
    80%,
    100% {
      box-shadow: 0 0;
      height: 4em; }
    40% {
      box-shadow: 0 -2em;
      height: 5em; } }
  @keyframes load1 {
    0%,
    80%,
    100% {
      box-shadow: 0 0;
      height: 4em; }
    40% {
      box-shadow: 0 -2em;
      height: 5em; } } }

/* img */
.img {
  &-responsive, &--responsive {
    display: block;
    height: auto;
    max-width: 100%; }

  &-round, &--round {
    border-radius: 50%; } }

/* section */
.section {
  padding: 60px 0 65px 0;
  min-width: 320px;
  &__head {
    color: #444444;
    font-family: $font-roboto;
    display: table;
    width: 100%;
    font-size: rem(25);
    text-transform: uppercase;
    font-weight: 400;
    margin-bottom: 20px;
    &:before, &:after {
      content: '';
      display: table-cell;
      background-image: linear-gradient(#dbdbdb, #dbdbdb);
      background-size: 100% 1px;
      background-repeat: no-repeat;
      background-position: center; }
    div {
      display: table-cell;
      white-space: nowrap;
      text-align: center;
      width: 100px;
      padding: 0 25px;
      position: relative;
      line-height: 1.1; } }
  &__more {
    text-align: center;
    background-image: linear-gradient(#dbdbdb, #dbdbdb);
    background-size: 100% 1px;
    background-repeat: no-repeat;
    background-position: center;
    &>a {
      display: inline-block;
      background: $color-1;
      border-radius: 25px;
      text-decoration: none;
      color: $color-white;
      height: 30px;
      line-height: 28px;
      width: 100%;
      max-width: 195px;
      text-transform: uppercase;
      font-family: $font-roboto-bold;
      font-size: rem(11);
      border: 1px solid $color-1;
      &:hover, &:focus {
        background: $color-white;
        color: $color-1; } } } }

.content {
  padding-bottom: 50px; }

ul li {
  list-style-type: disc; }
ol li {
  list-style-type: decimal; }

ul.list {
  margin: 0 0 25px 0;
  padding: 0;
  font-size: rem(14);
  // font-family: $font-roboto-medium
  li {
    line-height: 24px;
    list-style: none;
    margin: 0;
    padding-left: 32px;
    position: relative;
    &::before {
      content: "\f363";
      font-family: $font-ionicons;
      position: absolute;
      left: 17px;
      top: 5px;
      line-height: 1;
      color: $color-1;
      font-size: rem(14); } } }

ol.list {
  margin: 0 0 25px 0;
  padding: 0;
  font-size: rem(14);
  li {
    line-height: 24px;
    list-style: none;
    margin: 0;
    padding-left: 32px;
    position: relative;
    .span {
      display: block;
      left: 6px;
      top: 0;
      width: 19px;
      height: 19px;
      position: absolute;
      text-align: center;
      color: $color-white;
      background: $color-1;
      border-radius: 50%;
      font-size: 12.5px;
      font-family: $font-roboto-bold;
      text-align: right; } } }

/* bg */
.bg {
  &--pink {
    background-color: pink !important; }
  &--white {
    background-color: $color-white !important; }
  &--black {
    background-color: $color-black !important; } }

/* no padding no margin */
.no {
  &--p {
    padding: 0 !important;
    &-top {
      padding-top: 0 !important; }
    &-bottom {
      padding-bottom: 0 !important; }
    &-left {
      padding-left: 0 !important; }
    &-right {
      padding-right: 0 !important; } }

  &--m {
    margin: 0 !important;
    &-top {
      margin-top: 0 !important; }
    &-bottom {
      margin-bottom: 0 !important; }
    &-left {
      margin-left: 0 !important; }
    &-right {
      margin-right: 0 !important; } }

  &--b {
    background: none !important;
    &-image {
      background-image: none !important; } } }

/* icons */
.icon {
  display: inline-block;
  // &--sprite
 }  //   @include sprite($img-name)

/* color */
.color {
  &--black {
    color: $color-black !important; }
  &--white {
    color: $color-white !important; } }
