/* modules */

.news {
  &__block {
    position: relative;
    max-width: 345px;
    margin: 0 auto 20px;
    &-img {
      overflow: hidden;
      margin-bottom: 15px;
      position: relative;
      border-radius: 4px;
      img {
        opacity: 1;
        transition: opacity 0.35s;
        max-width: 100%;
        height: auto;
        display: block; } }
    &-date {
      position: absolute;
      top: 14px;
      left: 12px;
      background: $color-1;
      color: $color-white;
      border-radius: 25px;
      height: 20px;
      line-height: 20px;
      font-size: rem(10);
      font-family: $font-roboto-bold;
      width: 97px;
      text-align: center; }
    &-title {
      display: inline-block;
      text-decoration: none;
      font-size: rem(13);
      line-height: 18px;
      font-family: $font-roboto-medium;
      color: #444444;
      &:hover {
        color: $color-1; } }
    &:hover {
      .news__block {
        &-img {
          img {
            opacity: 0.5; } } } } }
  &__row {
    display: flex;
    flex-wrap: wrap; } }

.offers {
  background: #fafafa;
  border-radius: 4px;
  padding-top: 25px;
  padding-bottom: 30px;
  box-shadow: 15px 0 0 #fafafa, -15px 0 0 #fafafa;
  &__block {
    display: block;
    background: $color-white;
    overflow: hidden;
    border-radius: 4px;
    height: 207px;
    max-width: 345px;
    margin: 0 auto 22px;
    text-decoration: none;
    text-align: center;
    padding-top: 10px;
    &-title {
      font-family: $font-roboto-medium;
      font-size: rem(18);
      text-transform: uppercase;
      display: block;
      color: #444;
      line-height: 25px;
      margin-bottom: 5px;
      transition: color 0.35s; }
    &-info {
      font-size: rem(12);
      color: #9c9c9c;
      display: block;
      line-height: 1; }
    &-img {
      display: table;
      width: 100%;
      margin-bottom: 5px;
      &>span {
        display: table-cell;
        height: 120px;
        vertical-align: middle;
        text-align: center;
        img {
          display: inline-block; } } }
    &:hover {
      .offers__block-title {
        color: $color-1; } } } }

.about {
  background: #fafafa;
  &__img {
    overflow: hidden;
    border-radius: 4px;
    position: relative;
    img {
      max-width: 100%;
      height: auto;
      display: block; } }
  &__content {
    padding: 25px 40px 25px 0;
    font-size: rem(13);
    p {
      line-height: 20px;
      &:last-child {
        margin-bottom: 0; } } } }

.advantages {
  padding: 40px 0 45px 0;
  &__block {
    position: relative;
    padding-left: 83px;
    min-height: 57px;
    color: #808080;
    font-size: rem(13);
    &-img {
      position: absolute;
      left: 0;
      top: 0;
      width: 83px;
      height: 56px;
      text-align: center;
      img, svg {
        width: 67px;
        height: 56px;
        display: inline-block; }
      svg {
        path {
          fill: $color-1; } } }
    &-content {
      line-height: 18px;
      strong {
        display: block;
        color: $color-3;
        text-transform: uppercase; } } } }

.main-slider {
  margin-bottom: 0;
  padding: 0 40px;
  &__wrapper {
    overflow: hidden;
    border-radius: 4px;
    position: relative;
    .slick-dots {
      margin: 0;
      padding: 0;
      position: absolute;
      right: 22px;
      top: 50%;
      transform: translateY(-50%);
      li {
        list-style: none;
        margin: 0;
        line-height: 1;
        [role="button"] {
          border: 0;
          background: $color-white;
          font-size: 0;
          width: 14px;
          height: 14px;
          border-radius: 50%;
          overflow: hidden;
          opacity: 0.5;
          vertical-align: middle; }
        &+li {
          margin-top: 8px; }
        &.slick-active {
          [role="button"] {
            opacity: 1; } } } } }
  &__block {
    height: 520px;
    background-color: $color-3;
    overflow: hidden;
    border-radius: 4px;
    position: relative;
    text-decoration: none;
    display: block;
    background: {
      position: top center;
      repeat: no-repeat;
      size: cover; }
    &-gradient {
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      z-index: 5;
      background: linear-gradient(to right, rgba(#2975d3,.6), rgba(#07d0dd,.6)); }
    &-content {
      width: 100%;
      max-width: 1170px;
      padding: 0 15px;
      position: absolute;
      bottom: 50px;
      left: 50%;
      transform: translateX(-50%);
      z-index: 15;
      color: $color-white;
      font-family: $font-roboto-medium;
      font-size: rem(16);
      p {
        line-height: 20px;
        text-shadow: 0 2px 10px rgba($color-black,.5);
        max-width: 765px; } }
    &-number {
      position: absolute;
      left: -28px;
      top: 50%;
      transform: translateY(-50%);
      font-size: rem(100);
      opacity: 0.5;
      color: $color-white;
      font-family: $font-roboto-bold;
      line-height: 1;
      z-index: 15; } } }
